.App {
  text-align: center;
  border-color: rgb(70, 221, 171);
  background-color: #720455;
}


.App-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #030637;
  padding: 20px 20px;
  z-index: 1000; /* Ensure navbar is above other content */
  color: rgb(103, 194, 206);
}


.background-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("./asets/gambar.jpeg"); /* Ganti dengan path gambar Anda */
 /* Ganti dengan path gambar Anda */
  background-size: contain; /* Menggunakan contain agar gambar tidak dipotong */
  background-position: center;
  opacity: 0.5; /* Atur opasitas gambar sesuai kebutuhan */
  z-index: -1; /* Menempatkan latar belakang di belakang konten */
}



.App-link {
  color: #29b2d8;
}

.Movie-wrapper {
  width: 120px;
  height: fit-content;
  background-color: rgb(184, 171, 243);
  color: #3C0753;
  border: 2px solid #3C0753; 
  box-shadow: 11px 10px 19px 0px rgba(0,0,0,0.7);
-webkit-box-shadow: 11px 10px 19px 0px rgba(0,0,0,0.7);
-moz-box-shadow: 11px 10px 19px 0px rgba(0,0,0,0.7);
}




.Movie-search {
  height: 20px;
  font-size: 20px;
  margin-bottom: 15px;
  padding: 16px;
  width: 240px;
}

.Movie-container {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding-top: 200px;
}

.Movie-date {
  margin-top: 26px;
  font-style: italic;
}

.Movie-title {
  height: 60px;
  margin-top: 26px;
  font-weight: bold;
}

.Movie-rate {
  color: #e2186c;
  margin-bottom: 24px;
  font-weight: bold;
  font-size: 1.2em;
  text-decoration: underline;
}
